body, html {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 2px 0 0;
}

.logo a {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.nav {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: bold;
  list-style: none;
  display: flex;
}

.nav__link {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  text-decoration: none;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  top: auto;
  bottom: 0;
}
